function sanitizeAlg(algString) {
  return algString.replace(/[\(\)\[\]\{\}]/g, "");
}

function revertAlg(algString) {
  const cleaned = sanitizeAlg(algString);
  const parts = cleaned.split(" ").reverse();
  return parts
    .map(part => {
      if (part.endsWith("'")) {
        return part.slice(0, -1);
      } else if (part.endsWith("2")) {
        return part;
      } else {
        return part + "'";
      }
    })
    .join(" ");
}

// Hook
function useKeyPress(targetKey, outerUpHandler) {
  // Add event listeners
  useEffect(
    () => {
      // If pressed key is our target key then set to true
      function downHandler({ key }) {
        // if (key === targetKey) {
        // }
      }

      // If released key is our target key then set to false
      const upHandler = ({ key }) => {
        if (key === targetKey) {
          outerUpHandler();
        }
      };
      window.addEventListener("keydown", downHandler);
      window.addEventListener("keyup", upHandler);
      // Remove event listeners on cleanup
      return () => {
        window.removeEventListener("keydown", downHandler);
        window.removeEventListener("keyup", upHandler);
      };
    },
    [outerUpHandler, targetKey]
  ); // Empty array ensures that effect is only run on mount and unmount
}

const defaultSize = {width: 360, height: 384}
function Cube({ id, algorithm, className }) {
  const cube = useRef(
    TTk.AlgorithmPuzzle(3).size(defaultSize)
  );
  useEffect(
    () => {
      const sanitizedAlg = sanitizeAlg(algorithm);
      console.log("algorithm", sanitizedAlg);
      cube.current.case(sanitizedAlg)(`#${id}`);
    },
    [id, algorithm]
  );
  const size = useWindowSize();
  if (size.width != null && size.height != null) {
    const width = Math.min(size.width, defaultSize.width);
    const height = Math.min(size.height * 0.6, defaultSize.height);
    cube.current.size({width, height})
  }
  
  return <div id={id} className={className} />;
}

function PllCase({ activePllIndex }) {
  const pllKey = pllKeys[activePllIndex];
  const alg = pllAlgs[pllKey];
  const reversion = pllReversions[pllKey] || revertAlg(alg);

  return (
    <div>
      <div>
        <div className="text-sm text-gray-500">Setup {pllKey}:</div>
        <div className="ml-2 text-blue-50 tracking-widest font-bold">{reversion}</div>
      </div>
      <div>
        <div className="text-sm text-gray-500">Algorithm:</div>
        <div className="ml-2 text-blue-50 tracking-widest font-bold">{alg}</div>
      </div>
      <Cube id="pllCube" className="inline-block mx-auto" algorithm={alg} />
    </div>
  );
}

function getActiveStats(stats) {
  const activeStats = {};
  for (const key of Object.keys(stats)) {
    if (!stats[key].disabled) {
      activeStats[key] = stats[key].ranking;
    }
  }
  return activeStats;
}


const getRandomIdx = (prevIndex, stats, isFallback) => {
  const activeStats = getActiveStats(stats);
  const minStat = Math.min(...Object.values(activeStats));

  const candidates = []
  for (const key of pllKeys) {
    if (activeStats[key] === minStat) {
      candidates.push(key);
    }
  }

  const randomCandidateIndex = Math.floor(Math.random() * candidates.length);
  const randomIndex = pllKeys.indexOf(candidates[randomCandidateIndex]);
  const randomStat = activeStats[pllKeys[randomIndex]];

  if (!isFallback && prevIndex != null && prevIndex === randomIndex) {
    return getRandomIdx(prevIndex, stats, true);
  }
  return randomIndex;
};


