function tryParseJson(objString) {
  try {
    return JSON.parse(objString);
  } catch {
    return null;
  }
}

function App() {
  const [stats, setStats] = useState(tryParseJson(localStorage.getItem("stats")) || initialStats);
  const [activePllIndex, setActivePllIndex] = useState(getRandomIdx(null, stats));

  useInterval(() => {
    localStorage.setItem("stats", JSON.stringify(stats));
  }, 1000)

  const onNext = () => {
    const activeCaseKey = pllKeys[activePllIndex];
    setStats({
      ...stats,
      [activeCaseKey]: {...stats[activeCaseKey], ranking: stats[activeCaseKey].ranking + 1},
    })
    
    // Show next case
    setActivePllIndex(getRandomIdx(activePllIndex, stats));
  }

  const onEasy = () => {
    const activeCaseKey = pllKeys[activePllIndex];
    setStats({
      ...stats,
      [activeCaseKey]: {...stats[activeCaseKey], ranking: stats[activeCaseKey].ranking + 1},
    })
    
    // Show next case
    setActivePllIndex(getRandomIdx(activePllIndex, stats));
  };

  const onSkip = () => {
    setActivePllIndex(getRandomIdx(activePllIndex, stats));
  }

  const onToggleDisabled = (key) => {
    setStats({
      ...stats,
      [key]: {...stats[key], disabled: !stats[key].disabled},
    })
  }

  const onActivateAlg = algKey => {
    const idx = pllKeys.indexOf(algKey)
    setActivePllIndex(idx);
  }

  useKeyPress("n", onNext);
  useKeyPress("e", onEasy);

  if (activePllIndex === -1) {
    return "Invalid pll index"
  }

  const activePllKey = pllKeys[activePllIndex];

  return (
    <div className="App bg-gray-900">
      <FirebaseApp />
      <h1 className="text-blue-50 text-6xl font-thin tracking-widest my-8">Cube Coach</h1>
      <PllCase activePllIndex={activePllIndex} />
      <div>
        <button onClick={onNext} className="btn bg-blue-500">Next (+1)</button>
        <button onClick={onEasy} className="btn bg-green-500  ml-8">Next (+10)</button>
        <button onClick={onSkip} className="btn bg-blue-500  ml-8">Skip</button>
      </div>
      <AlgorithmSelection stats={stats} activePllKey={activePllKey} onToggleDisabled={onToggleDisabled} onActivateAlg={onActivateAlg} />
      <div id="cube" />
      <div id="mini-cube" />

      <div style={{ position: "absolute", top: 0, left: 0 }}>
        <div id="description_header" />
        <div id="description" />
      </div>

    </div>
  );
}

function AlgorithmButton({algKey,stats, activePllKey, onToggleDisabled, onActivateAlg}) {
  const isActive = activePllKey === algKey;
  const color = (stats[algKey].disabled ? "bg-gray-400" : "bg-blue-500");
  const optRing = isActive ? "border-4 border-green-500" : "";

  const onClick = () => onToggleDisabled(algKey);
  const onLongPress = () => onActivateAlg(algKey)
  const onPress = useLongPress(onLongPress, onClick);

  return  <li {...onPress} className={`${color} ${optRing} rounded-xl text-green-50 p-4 mb-4 mr-4 w-20`}>
    <div className="font-bold">{algKey}</div>
    <div className="text-sm">{stats[algKey].ranking}</div>
  </li>
}

function AlgorithmSelection({stats, activePllKey, onToggleDisabled, onActivateAlg}) {
  return <div className="mx-auto inline-block mt-8"><ul className="flex flex-wrap">
        {pllKeys.map(key =>
          <AlgorithmButton key={key} algKey={key} stats={stats} activePllKey={activePllKey} onToggleDisabled={onToggleDisabled} onActivateAlg={onActivateAlg} />
        )}
        </ul>
      </div>
}

