export const useAuth = () => {
  const [state, setState] = useState(() => {
    const user = firebase.auth().currentUser
    return {
      initializing: !user,
      user,
    }
  })

  function onChange(user) {
    setState({ initializing: false, user })
  }

  useEffect(() => {
    // listen for auth state changes
    const unsubscribe = firebase.auth().onAuthStateChanged(onChange)

    // unsubscribe to the listener when unmounting
    return () => unsubscribe()
  }, [])

  return state;
}

export function User({user}) {

  const { data, update, error } = useDocument(`statistics/${user.uid}`)
  // const { data, update, error } = useDocument(`/statistics/6R7LIVWHbTXHs4Nv7IYF3nVaY472`)
  console.log("user.id", user.id);
  console.log("error", error);
  console.log("data", data);
  if (error) return <span>Error!</span>
  if (!data) return <span>Loading...</span>


  const onUpdate = () => {
    update({"obj": {someProperty: data.obj.someProperty + 1}})
  }

  return <div>
    <button className="btn bg-gray-400" onClick={onUpdate}>Update</button>
    <span>Name: {data.name} ({data.obj.someProperty})</span>
  </div>
}


export function FirebaseApp() {
  const authState = useAuth();

  const showLogin = () => {
    const ui = new firebaseui.auth.AuthUI(firebase.auth());
    // The start method will wait until the DOM is loaded.
    ui.start('#firebaseui-auth-container', uiConfig);
  }
  const signOut = () => {
    firebase.auth().signOut();
  }

  const isLoggedOut = !authState.initializing && authState.user == null;
  const isLoggedIn = !authState.initializing && authState.user != null;

  return (
    <FuegoProvider fuego={fuego}>
      {isLoggedOut ? <button onClick={showLogin}>Log In</button> : null}
      {isLoggedIn ? <button onClick={signOut}>Log out</button> : null}
      <UserHeader />
      {isLoggedIn ? <User user={authState.user} /> : null}
    </FuegoProvider>
  )
}

export default App;
